import React from 'react';
import {useSelector,useDispatch}  from 'react-redux';
//import { Link } from '@material-ui/core';
import { Link,useHistory } from 'react-router-dom';

import { makeStyles } from '@material-ui/core/styles';

import {LogOutButton} from './parts/AuthButtons';
import { Typography } from '@material-ui/core';



const menuStyle=makeStyles((theme)=>({
    door:{
      width:"100%"
    },
    
    loginButtonBefore:{
        animation: "$flash 4s linear infinite",
        background:'gray'
      },

      listShelf: {
        padding: 0,
        //position: "relative"
      },
      
      column: {
        color: "#2d8fdd",
        borderLeft: "solid 6px #2d8fdd",/**/
        background: "#f1f8ff",/**/
        marginBottom: "3px",/**/
        lineHeight: 1.5,
        padding: "0.5em"
      }

}));

const SideMenu=({onClose=null})=>{
    const dispatch=useDispatch();
    const classes=menuStyle();
    const history=useHistory();
    const userRank=useSelector(state=>state.auth_login.userRank);

    const onMove=(path="none")=>{
        console.log("close");
        if (path!=="none"){
          history.push(path);
        }
        if (onClose){
          onClose();
        }

    };

    const menuList=[
      {path:"/favorite" , name:""},
      {path:"/notice" , name:""},
      {path:"/tagconfig" , name:""},
    ];

    return (
        <div>
            <div className={classes.listShelf}>
            <div className={classes.column}>
            <LogOutButton />
            </div>
            {
              menuList.map((menu)=>(
                <div className={classes.column} onClick={()=>onMove(menu.path)}><Typography variant='subtitle1' gutterBottom > {menu.name} </Typography></div>
              ))
            }
            {userRank>1 ?
              <div className={classes.column} onClick={()=>onMove("/tagadmin")}><Typography variant='subtitle1' gutterBottom >  </Typography></div>
              :
              <div/>
            }

          </div>
        </div>

    )
    
}

export default SideMenu;
        